'use client';

import { useState, useEffect, FormEvent, ChangeEvent } from 'react';
import axios from '@/lib/axios';
import Image from 'next/image';
import { toast } from 'sonner';
import { Plus, Pencil, Trash2, Loader2 } from 'lucide-react';

// Komponen UI
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogFooter,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from '@/components/ui/dialog';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

// Tipe Data Kategori
interface Kategori {
    id: number;
    nama_kategori: string;
    deskripsi: string;
    icon_url: string;
}

export default function KategoriPage() {
    // State Data
    const [kategoris, setKategoris] = useState<Kategori[]>([]);
    const [loading, setLoading] = useState(true);
    
    // State Modal & Form
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [isSaving, setIsSaving] = useState(false);
    const [editId, setEditId] = useState<number | null>(null); // ID jika sedang edit
    const [formData, setFormData] = useState({
        nama_kategori: '',
        deskripsi: '',
        icon: null as File | null,
    });

    // 1. Fetch Data (GET)
    const fetchKategoris = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/kategoris');
            setKategoris(response.data);
        } catch (error) {
            console.error("Error fetching data:", error);
            toast.error("Gagal memuat data kategori.");
        } finally {
            setLoading(false);
        }
    };

    // Panggil saat halaman dimuat
    useEffect(() => {
        fetchKategoris();
    }, []);

    // 2. Reset Form
    const resetForm = () => {
        setFormData({ nama_kategori: '', deskripsi: '', icon: null });
        setEditId(null);
        setIsDialogOpen(false);
    };

    // 3. Handle Buka Modal (Tambah / Edit)
    const handleOpenDialog = (kategori?: Kategori) => {
        if (kategori) {
            // Mode Edit
            setEditId(kategori.id);
            setFormData({
                nama_kategori: kategori.nama_kategori,
                deskripsi: kategori.deskripsi || '',
                icon: null
            });
        } else {
            // Mode Tambah
            setEditId(null);
            setFormData({ nama_kategori: '', deskripsi: '', icon: null });
        }
        setIsDialogOpen(true);
    };

    // 4. Handle Input Perubahan
    const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            setFormData({ ...formData, icon: e.target.files[0] });
        }
    };

    // 5. Handle Submit (Create & Update)
    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        setIsSaving(true);

        try {
            if (editId) {
                // --- MODE EDIT (UPDATE) ---
                
                // 1. Update Data Teks (JSON)
                await axios.put(`/api/kategoris/${editId}`, {
                    nama_kategori: formData.nama_kategori,
                    deskripsi: formData.deskripsi
                });

                // 2. Update Gambar (JIKA ADA FILE BARU)
                if (formData.icon) {
                    const imageData = new FormData();
                    imageData.append('icon', formData.icon);
                    
                    await axios.post(`/api/kategoris/${editId}/icon`, imageData, {
                        headers: { 'Content-Type': 'multipart/form-data' },
                    });
                }

                toast.success("Kategori berhasil diperbarui!");

            } else {
                // --- MODE TAMBAH (CREATE) ---
                const data = new FormData();
                data.append('nama_kategori', formData.nama_kategori);
                data.append('deskripsi', formData.deskripsi);
                if (formData.icon) {
                    data.append('icon', formData.icon);
                }

                await axios.post('/api/kategoris', data, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });

                toast.success("Kategori berhasil ditambahkan!");
            }

            resetForm();
            fetchKategoris(); // Refresh tabel
            
        } catch (error: any) {
            console.error(error);
            const pesan = error.response?.data?.message || "Gagal menyimpan data.";
            if (error.response?.data?.errors?.icon) {
                toast.error(error.response.data.errors.icon[0]);
            } else {
                toast.error(pesan);
            }
        } finally {
            setIsSaving(false);
        }
    };

    // 6. Handle Delete
    const handleDelete = async (id: number) => {
        if(!confirm("Yakin ingin menghapus kategori ini?")) return;

        try {
            await axios.delete(`/api/kategoris/${id}`);
            toast.success("Kategori berhasil dihapus");
            fetchKategoris();
        } catch (error) {
            toast.error("Gagal menghapus kategori");
        }
    }

    return (
        <div className="space-y-6">
            {/* Judul & Tombol Tambah */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Data Kategori</h1>
                    <p className="text-gray-500 text-sm">Kelola kategori produk untuk POS dan Inventaris.</p>
                </div>
                
                <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
                    <DialogTrigger asChild>
                        <Button 
                            className="bg-[#D93F21] hover:bg-[#b9351b]"
                            onClick={() => handleOpenDialog()}
                        >
                            <Plus className="w-4 h-4 mr-2" />
                            Tambah Kategori
                        </Button>
                    </DialogTrigger>
                    
                    <DialogContent className="sm:max-w-[425px]">
                        <DialogHeader>
                            <DialogTitle>{editId ? 'Edit Kategori' : 'Tambah Kategori Baru'}</DialogTitle>
                            <DialogDescription>
                                {editId ? 'Perbarui informasi kategori di bawah ini.' : 'Masukkan detail kategori baru di bawah ini.'}
                            </DialogDescription>
                        </DialogHeader>
                        
                        <form onSubmit={handleSubmit} className="space-y-4 mt-2">
                            <div className="space-y-2">
                                <Label htmlFor="nama">Nama Kategori <span className="text-red-500">*</span></Label>
                                <Input 
                                    id="nama" 
                                    placeholder="Contoh: Makanan Berat" 
                                    value={formData.nama_kategori}
                                    onChange={(e) => setFormData({...formData, nama_kategori: e.target.value})}
                                    required
                                />
                            </div>
                            
                            <div className="space-y-2">
                                <Label htmlFor="deskripsi">Deskripsi</Label>
                                <Textarea 
                                    id="deskripsi" 
                                    placeholder="Keterangan singkat..." 
                                    value={formData.deskripsi}
                                    onChange={(e) => setFormData({...formData, deskripsi: e.target.value})}
                                />
                            </div>
                            
                            <div className="space-y-2">
                                <Label htmlFor="icon">Ikon (Gambar)</Label>
                                <Input 
                                    id="icon" 
                                    type="file" 
                                    accept="image/*"
                                    onChange={handleFileChange}
                                    className="cursor-pointer"
                                />
                                <p className="text-xs text-gray-500">Format: JPG, PNG. Maks 2MB.</p>
                            </div>

                            <DialogFooter className="pt-4">
                                <Button type="button" variant="outline" onClick={resetForm}>
                                    Batal
                                </Button>
                                <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                                    {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                                    {editId ? 'Perbarui' : 'Simpan'}
                                </Button>
                            </DialogFooter>
                        </form>
                    </DialogContent>
                </Dialog>
            </div>

            {/* Tabel Data */}
            <Card>
                <CardHeader className="pb-3">
                    <CardTitle className="text-lg">Daftar Kategori</CardTitle>
                </CardHeader>
                <CardContent>
                    <div className="rounded-md border">
                        <Table>
                            <TableHeader>
                                <TableRow className="bg-gray-50">
                                    <TableHead className="w-[80px]">Ikon</TableHead>
                                    <TableHead>Nama Kategori</TableHead>
                                    <TableHead className="hidden md:table-cell">Deskripsi</TableHead>
                                    <TableHead className="text-right">Aksi</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {loading ? (
                                    <TableRow>
                                        <TableCell colSpan={4} className="h-24 text-center text-gray-500">
                                            <div className="flex justify-center items-center gap-2">
                                                <Loader2 className="h-5 w-5 animate-spin" /> Memuat data...
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                ) : kategoris.length === 0 ? (
                                    <TableRow>
                                        <TableCell colSpan={4} className="h-24 text-center text-gray-500">
                                            Belum ada data kategori.
                                        </TableCell>
                                    </TableRow>
                                ) : (
                                    kategoris.map((item) => (
                                        <TableRow key={item.id} className="hover:bg-gray-50">
                                            <TableCell>
                                                <div className="relative h-10 w-10 rounded-lg overflow-hidden bg-gray-100 border border-gray-200">
                                                    {item.icon_url && item.icon_url !== "" ? (
                                                        <Image 
                                                            src={item.icon_url} 
                                                            alt={item.nama_kategori}
                                                            fill
                                                            className="object-cover"
                                                            unoptimized 
                                                            
                                                            onError={(e) => {
                                                                (e.target as HTMLElement).style.display = 'none'; 
                                                            }}
                                                        />
                                                    ) : (
                                                        <div className="flex items-center justify-center w-full h-full text-gray-400 text-xs font-bold">
                                                            {item.nama_kategori.charAt(0).toUpperCase()}
                                                        </div>
                                                    )}
                                                </div>
                                            </TableCell>
                                            <TableCell className="font-medium text-gray-900">
                                                {item.nama_kategori}
                                            </TableCell>
                                            <TableCell className="hidden md:table-cell text-gray-500 text-sm">
                                                {item.deskripsi || '-'}
                                            </TableCell>
                                            <TableCell className="text-right">
                                                <div className="flex justify-end gap-2">
                                                    <Button 
                                                        variant="ghost" 
                                                        size="icon" 
                                                        className="h-8 w-8 text-blue-600 hover:text-blue-800 hover:bg-blue-50"
                                                        onClick={() => handleOpenDialog(item)}
                                                    >
                                                        <Pencil className="h-4 w-4" />
                                                    </Button>
                                                    <Button 
                                                        variant="ghost" 
                                                        size="icon" 
                                                        className="h-8 w-8 text-red-600 hover:text-red-800 hover:bg-red-50"
                                                        onClick={() => handleDelete(item.id)}
                                                    >
                                                        <Trash2 className="h-4 w-4" />
                                                    </Button>
                                                </div>
                                            </TableCell>
                                        </TableRow>
                                    ))
                                )}
                            </TableBody>
                        </Table>
                    </div>
                </CardContent>
            </Card>
        </div>
    );
}